html {
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}
@media (min-width: 40em) {
  html {
    overflow-y: hidden;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font: var(--font);
  font-size: 1rem;
  min-height: 150vh;
  background: url(..//images/pattern-background-desktop.svg) no-repeat;
}
@media (max-width: 23.4375em) {
  body {
    background: var(--vrypaleblue) url(..//images/pattern-background-mobile.svg) no-repeat;
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --lightpurple: hsl(243, 78%, 82%);
  --hotpink: hsl(338, 100%, 69%);
  --offsetblue: hsl(240, 100%, 73%);
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --vrypaleblue: hsl(225, 100%, 98%);
  --desturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(223, 47%, 23%);
  --offwhite: hsl(225, 50%, 98%);
}

:root {
  --font: font-family: "Red Hat Display", sans-serif;
}

@media (max-width: 39.9375em) {
  .col-12 {
    padding-top: 100px;
    padding-left: 23px;
    padding-right: 23px;
  }
}
@media (min-width: 40em) {
  .col-12 {
    display: grid;
    grid-template-columns: 3, 1fr;
    grid-template-rows: 3, 1fr;
    height: 40vh;
    justify-items: center;
    align-items: flex-end;
  }
}

@media (max-width: 39.9375em) {
  .col-sm-8 {
    padding-left: 23px;
    padding-right: 23px;
  }
}
@media (min-width: 40em) {
  .col-sm-8 {
    max-width: 30.0625rem;
    left: 9rem;
  }
}
@media (min-width: 55em) {
  .col-sm-8 {
    left: 16.875rem;
  }
}
@media (min-width: 71.875em) {
  .col-sm-8 {
    left: 29.9375rem;
  }
}

@media (min-width: 40em) {
  .card-img {
    max-width: 28.125rem;
    max-height: 13.75rem;
  }
}

.card-body {
  padding-bottom: 0px;
}

.card-body h2 {
  font-weight: 900;
  font-size: 1rem;
  text-align: center;
  color: var(--darkblue);
  margin-top: 10px;
}
@media (min-width: 25.4375em) {
  .card-body h2 {
    font-size: 1.125rem;
  }
}

.card-body p {
  font-weight: 500;
  font-size: 0.875rem;
  text-align: center;
  color: var(--desturatedblue);
  margin-top: 20px;
  line-height: 1.375rem;
  margin-bottom: 0px;
}
@media (min-width: 40em) {
  .card-body p {
    font-size: 1rem;
    margin-left: 1.5625rem;
    margin-right: 1.5625rem;
  }
}
@media (min-width: 25.4375em) {
  .card-body p {
    font-size: 0.9375rem;
  }
}
@media (min-width: 40em) {
  .card-body p {
    margin-left: 50px;
    margin-right: 50px;
  }
}

.card-plan {
  margin: 20px;
  border-radius: 0.625rem;
  display: grid;
  max-width: 20.5rem;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(1, 1fr);
  background: var(--vrypaleblue);
}
@media (min-width: 55em) {
  .card-plan {
    max-width: 26.25rem;
  }
}
@media (min-width: 25.4375em) {
  .card-plan {
    margin-left: 35px;
    margin-right: 35px;
  }
}
@media (min-width: 40em) {
  .card-plan {
    max-width: 18.75rem;
    margin-left: 75px;
  }
}
.card-plan-image {
  width: 42px;
  height: 42px;
  align-self: center;
  margin-left: 12px;
}
@media (min-width: 25.4375em) {
  .card-plan-image {
    width: 48px;
    height: 48px;
  }
}
.card-plan-subscription {
  grid-row: span 2;
  grid-column: span 2;
  line-height: 1.5625rem;
  align-self: center;
  margin-right: 22px;
}
@media (min-width: 25.4375em) {
  .card-plan-subscription {
    grid-row: span 3;
    margin-right: 50px;
  }
}
.card-plan-subscription h4 {
  font-weight: 900;
  font-size: 0.75rem;
  text-align: center;
  color: var(--darkblue);
  margin-top: 15px;
}
@media (min-width: 25.4375em) {
  .card-plan-subscription h4 {
    font-size: 0.875rem;
  }
}
.card-plan-subscription p {
  font-weight: 500;
  font-size: 0.875rem;
  text-align: center;
  color: var(--desturatedblue);
  line-height: 0.625rem;
}
.card-plan-change {
  align-self: center;
  margin-right: 12px;
}
.card-plan-change p {
  color: var(--brightblue);
  font-weight: 900;
  font-size: 0.75rem;
  text-align: center;
  padding-top: 20px;
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-decoration-thickness: auto;
  text-decoration-color: var(--brightblue);
  transition: all 300ms ease-in-out;
}
.card-plan-change p:hover {
  color: var(--desturatedblue);
  text-decoration: none;
}
@media (min-width: 25.4375em) {
  .card-plan-change p {
    font-size: 0.875rem;
  }
}

.card-buttons {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

.card-buttons button {
  font-weight: bold;
  font-size: 14px;
  color: var(--offwhite);
  box-shadow: 5px 15px 15px 0px var(--desturatedblue);
  cursor: pointer;
  background: var(--brightblue);
  border-radius: 0.625rem;
  width: 14.5rem;
  height: 2.8125rem;
  margin: auto;
  transition: all 300ms ease-in-out;
}
.card-buttons button:hover {
  color: var(--darkblue);
  text-decoration: none;
}
@media (min-width: 23.4375em) {
  .card-buttons button {
    width: 17.9375rem;
    height: 3rem;
  }
}
@media (min-width: 25.4375em) {
  .card-buttons button {
    width: 18.75rem;
    height: 3.125rem;
  }
}

.card-buttons-cancel p {
  text-align: center;
  padding-top: 1.25rem;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 0.875rem;
  text-align: center;
  color: var(--desturatedblue);
  transition: all 300ms ease-in-out;
}
.card-buttons-cancel p:hover {
  color: var(--darkblue);
  text-decoration: none;
}